<template>
    <div style="width : 65%">
        <a-typography-title class="title">
            Deck Search
        </a-typography-title>
        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="search" label="search">
                <a-input v-model="form.search" placeholder="input your query" />
            </a-form-item>

            <a-form-item>
                <a-button @click="searchMethod">Submit</a-button>
            </a-form-item>
        </a-form>
    </div>
    <div>
        <a-form :model="form" class="infor-form">
            <a-form-item label="Word" :columns="columns" :data="data">
                <a-typography-text editable v-model:editText="word" @edit-end="updateInfors">
                    {{ word }}
                    
                </a-typography-text>
            </a-form-item>
            <a-form-item label="meaning" :columns="columns" :data="data">
                <a-typography-paragraph editable v-model:editText="email" @edit-end="updateInfors">
                    {{ meaning }}
                </a-typography-paragraph>
            </a-form-item>

            <a-form-item>
                <a-button @click="searchMethod">Submit</a-button>
            </a-form-item>
        </a-form>
    </div>
</template>

<script>

import { inject, ref, reactive } from 'vue'
import { Modal } from '@arco-design/web-vue';


export default {
    name: 'SearchComponent',
    setup() {
        const layout = ref('vertical')
        const form = reactive({});
        const $axios = inject("$axios");
        const columns = [

            {
                title: 'word',
                dataIndex: "words"
            },
            {
                title: 'meaning',
                dataIndex: 'meanings',
            },

        ];

        var data = reactive([]);


        const word = ref();
        const meaning = ref();

        const searchMethod = () => {
            $axios({
                url: "/anki/searchDecks.do",
                method: "post",
                data: "deck_name=" + form.search,
                headers: {}
            }).then((res) => {
                if (res.data.status == 200) {
                    console.log(res);
                    var tempdata = res.data.data;
                    console.log(tempdata);
                    for (var i = 0; i < tempdata.length; i++) {
                        var obj = tempdata[i];
                        obj.words = obj.word;
                        obj.meanings = obj.meaning;
                        data.push(obj);
                    }
                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })
        }

        const updateInfors = () => {
            console.log(word.value);
            console.log(meaning.value);

            $axios({
                url: "/anki/editDecks.do",
                method: "post",
                data: "word=" + word.value + "&meaning=" + meaning.value ,
                headers: {

                }
            }).then((res) => {
                console.log(res);
            })
        }

        return {
            layout,
            form,
            data,
            columns,
            updateInfors,
            searchMethod,
            word,
            meaning
        }
    }
}
</script>

<style>
.title {
    margin-left: 2%;
    text-align: left;
}

/* .arco-layout-content {
    display: inline;
} */

.form-box {
    margin-left: 2%;
    text-align: left;
}
</style>